@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700&family=Raleway:wght@300;400;600;700&display=swap");
* {
  margin: 0;
  padding: 0;
}

div.header {
  width: 100vw;
}
div.header div.container div.main_logo_navigation {
  width: 100%;
  height: 70px;
  display: flex;
}
div.header div.container div.main_logo_navigation div.logo {
  font-size: 32px;
  color: #08a6e4;
}
body {
  box-sizing: border-box;
  font-family: "Open-Sans", sans-serif;
}

.container {
  width: 100%;
  padding: 0 15px;
}
@media only screen and (min-width:1352px) {
  .container {
    width: 1352px;
    margin: 0 auto;
  }
}